import {
  JupyterFrontEnd,
  JupyterFrontEndPlugin
} from '@jupyterlab/application';
import {
  ICommandPalette,
  IThemeManager,
  IToolbarWidgetRegistry
} from '@jupyterlab/apputils';

import { INotebookTracker } from '@jupyterlab/notebook';
import { ILauncher } from '@jupyterlab/launcher';

import {
  tokens,
  CoreExtension,
  ShareNotebookPluginOpts
} from '@anaconda/shared';

import { ShareNotebookPlugin } from '@anaconda/share_notebook_lib';

const share_notebookPlugin = new ShareNotebookPlugin();

/**
 * Initialization data for the @anaconda/share_notebook extension.
 */
const plugin: JupyterFrontEndPlugin<void> = {
  id: '@anaconda/share_notebook:plugin',
  description: 'A JupyterLab extension.',
  autoStart: true,
  requires: [
    tokens.CoreToken,
    ILauncher,
    INotebookTracker,
    ICommandPalette,
    IThemeManager,
    IToolbarWidgetRegistry
  ],
  activate: (
    app: JupyterFrontEnd,
    coreExtension: CoreExtension,
    launcher: ILauncher,
    notebookTracker: INotebookTracker,
    palette: ICommandPalette,
    themeManager: IThemeManager,
    toolbarRegistry: IToolbarWidgetRegistry
  ) => {
    console.log('@anaconda/share_notebook:plugin extension is activated');
    const { corePlugin } = coreExtension;
    const share_notebookOpts: ShareNotebookPluginOpts = {
      themeManager,
      corePlugin,
      notebookTracker,
      palette,
      toolbarRegistry
    };

    share_notebookPlugin.activate(app, share_notebookOpts);
  }
};

export default plugin;
